.title {
  display: flex;
  color: $text-white;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
  justify-content: center;

  margin-top: 0;

  @include onTablet {
    font-size: 48px;
    line-height: 48px;
  }

  @include onDesktop {
    font-size: 64px;
    line-height: 64px;
  }
}
